<template>
  <div class="hello">
    <div class="counter">
      <p>货柜</p>
      <div class="wrap" id="wrap"  @drop="drag($event)" @dragover="over($event)">
        <ul class="selGoods">
          <li v-for="(ele,index) in selGoods" @drop="ele[0].width===100?'':dragInLi($event,index)" @dragover="goodsOver($event,index)"
              draggable="false" :style="{width:ele.length>1?competeType(ele)===30?'18%':'20%':getMaxWidth(ele),'display':ele.length===1?'flex':'block',
                'padding-top':ele.length===1?'0':'3%',transform:ele.length>1?'rotate(180deg)':'' }">
            <img v-if="ele.length===1" v-for="(e,i) in ele" @click="showInfor(index,i)" @contextmenu="del($event,index,i)"
                 :style="{width:e.width===100?'100%':e.width===50?'90%':'110%',height:e.width===100?'70%':e.width===50?'40%':'25%',
            '-webkit-box-flex':e.width===100?'3':e.width===50?'2':'1',transform:ele[0].width===30?'translateY(90%)':ele[0].width===50?'translateY(30%)':''}" :src="e.img" alt="" draggable="false">

            <img class="img9" v-if="ele.length>1&&competeType(ele)===30" v-for="(e,i) in ele" @click="showInfor(index,i)"
                 @contextmenu="del($event,index,i)" :src="e.img" alt="" draggable="false"
                 :style="{float:'right',transform:'rotate(180deg)'}">

            <img class="img4" v-if="ele.length>1&&competeType(ele)===50" v-for="(e,i) in ele" @click="showInfor(index,i)" @contextmenu="del($event,index,i)"
                 :style="{float:'right',transform:'rotate(180deg)'}"
                 :src="e.img" alt="" draggable="false">

            <div class="block4" v-if="ele.length>1&&competeType(ele)==='mix'" :da=i v-for="(e,i) in ele" @click="showInfor(index,i)" draggable="false"
                 @contextmenu="del($event,index,i)" :style="{float:'right',transform:'rotate(180deg)'}">
              <img :src="e.img" alt="" draggable="false"
              :style="{width:e.width===30?'58%':'90%',height:e.width===30?'66%':'85%','margin-top':e.width===30?'13%':''}">
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="all">
      <p>物品栏</p>
      <div class="search">搜索：<input type="text" placeholder="输入搜索内容..."></div>
      <ul class="allGoods">
        <li v-for="(e,i) in goodOnShow" @dragstart="startDrag($event,i)" draggable="true">
          <img :style="{width:e.width===100?'65%':e.width===50?'55%':'45%',
          height:e.width===100?'85%':e.width===50?'75%':'65%',
          marginTop:e.width===100?'3%':e.width===50?'5%':'8%'}"
               :src="e.img" alt="" draggable="true">
        </li>
      </ul>
      <div class="page">
        <ul>
          <li :class="page==i?'sel':''" v-for="i in pages" @click="changePage(i)">
            {{i}}
          </li>
        </ul>
      </div>
    </div>
    <input class="full" value="全屏" type='button' @click='fullScreen();' />
    <div class="detail" v-if="showDetail">
      <span @click = 'closeInfor'>×</span>
      <img :src="goodDetail.img" alt="">
      <p><span>商品名字 ：{{goodDetail.name}}</span><span></span></p>
      <p><span>编号 ：{{goodDetail.number}}</span><span>价格 ：{{goodDetail.price}}</span></p>
    </div>
  </div>
</template>

<script>
  import {goods} from './goods'
  export default {
    name: 'HelloWorld',
    data () {
      return {
        allGoods:goods,
        goodOnShow:[],
        selGoods:[],
        goodDetail:{},
        showDetail:false,

        page:1,
        pages:0,
      }
    },
    mounted(){
      this.pages = this.allGoods.length%24>0?
        parseInt(this.allGoods.length/24) +1 :
        this.allGoods.length/24

      this.changePage(1)
    },
    methods:{
      changePage(i){
        this.page = i;
        this.goodOnShow = this.allGoods.slice(24*(i-1),24*i)
      },
      startDrag(e,i){
        e.dataTransfer.setData("index" , i);
      },
      goodsOver(e,i){
        if(this.selGoods[i][0].width!==100) {
          e.preventDefault();
          e.stopPropagation();
          this.resetAllStyle();
          document.querySelectorAll('.selGoods li')[i].style.border = '2px solid green';
        }
      },
      drag(ev){
        let index = ev.dataTransfer.getData("index");
        this.selGoods.push([this.goodOnShow[index]]);
        setTimeout(()=>{
          let wrap = document.querySelector('#wrap').clientHeight;
          let selGoods = document.querySelector('#wrap .selGoods').clientHeight;
          if(selGoods>wrap){
            alert("商品槽位已满");
            this.selGoods.pop();
          }
        },0)

        this.resetAllStyle()
      },
      dragInLi(e,i){
        e.stopPropagation()
        let type = this.competeType(this.selGoods[i])
        let max;
        let index = e.dataTransfer.getData("index");

        switch (type){
          case 30: max=9;break ;
          case 50:case 'mix':max=4;break;
        }
        if(max===9&&this.selGoods[i].length>3){
          if(this.goodOnShow[index].width!==30){
            alert('当前商品尺寸及数量与拖入商品尺寸不匹配')
          }else if(this.selGoods[i].length>=max){
            alert('已达数量上限')
          }else{
            this.selGoods[i].push(this.goodOnShow[index]);
          }
        }else if(this.selGoods[i].length>=max){
          alert('已达数量上限')
        }else {
          if(this.goodOnShow[index].width===100){
            alert('尺寸不符合要求')
          }else {
            this.selGoods[i].push(this.goodOnShow[index]);
          }
        }
        this.resetAllStyle()
      },
      over(e){
        e.preventDefault();
      },
      resetAllStyle(){
        document.querySelectorAll('.selGoods li').forEach((e)=>{
          e.style.border = '0';
        })
      },
      del(e,index,i){
        e.preventDefault();

        this.selGoods[index].splice(i,1);
        if(this.selGoods[index].length===0)
          this.selGoods.splice(index,1);
      },
      showInfor(index,i){
        this.showDetail = true;
        this.goodDetail = this.selGoods[index][i]
      },
      closeInfor(){
        this.showDetail = false;
      },
      fullScreen(){
        let element = document.documentElement;
        if(element.requestFullscreen) {
          element.requestFullscreen();
        } else if(element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if(element.webkitRequestFullscreen) {
          element.webkitRequestFullscreen();
        } else if(element.msRequestFullscreen) {
          element.msRequestFullscreen();
        }
      },
      getMaxWidth(ele){
        let all= ele.map((e)=>e.width);
        let max = Math.max.apply(null, all);
        return max===100?'20%':max===50?'10%':'6%'
      },
      competeType(el){
        let type;
        el.every((e)=>e.width===30) ? type=30:
          el.every((e)=>e.width===50) ? type=50:type='mix';
        return type
      }
    }
  }
</script>

<style scoped lang="scss">
  *{
    padding: 0;
    margin: 0;
    user-select: none;
  }
  .hello{
    width:86vw;
    margin:0 auto;
    padding-top:7.5vh;
  }
  .counter>p,.all>p{
    box-sizing: border-box;
    width:100%;
    height:10%;
    background-color: pink;
    text-align: center;
    font-size:25px;
    font-weight:bolder;
  }
  .all{
    margin-left:3vw;
    width:30vw;
    height: 85vh;
    float: left;

  }

  .counter{
    width:52vw;
    height: 85vh;
    float: left;
    .wrap {
      height: 90%;
      width: 100%;
      border: 1px solid pink;
      box-sizing: border-box;

      ul {
        width: 100%;
        list-style: none;
        background-image: url(../assets/bg.jpg);
        background-position: 0 103%;
        background-size: 100% 15.5vh;

        &:after {
          content: '';
          display: block;
          height: 0;
          visibility: hidden;
          clear: both;
        }
        li {
          height: 15.2vh;
          width: 25%;
          float: left;
          box-sizing: border-box;
          text-align: center;
          overflow-y: auto;
          padding-left:1%;
          padding-right:1%;
          display: flex;
          align-items:center;

          img {
            -webkit-box-flex:1;
            height: 75%;
            margin-top: 3%;
            width: 70%;
            box-shadow: 2px 2px 5px 0 gray;
            cursor: pointer;
            margin-right:5px;

          }
          .img{
            width:30%;
            height:33%;
            margin-right:3%;
            margin-bottom:3%;
            float: left;
          }
          .img9{
            width:30%;
            height:30%;
            margin-right:3%;
            margin-bottom:11%;
            float: left;
            margin-top:-10%
          }
          .img4{
            width:45%;
            height:45%;
            margin-right:5%;
            margin-bottom:13%;
            margin-top:-10%;
            float: left;
          }
          .block4{
            width:50%;
            height:50%;
            float: left;
            margin-top:-4%;

          }
        }
      }
    }
  }
  .all .search{
    height:4%;
    border-left:1px solid pink;
    border-right:1px solid pink;
  }
  .all .search input{
    height:70%;
    width:50%;

  }
  .all .allGoods{
    height:80%;
    width:100%;
    list-style: none;
    border:1px solid pink;
    box-sizing: border-box;
  }
  .all .allGoods li{
    height:12.5%;
    width:33.3%;
    float: left;
    border:1px solid pink;
    box-sizing: border-box;
    text-align: center;
    line-height:12.5%

  }
  .all .allGoods li img{
    height:80%;
  }
  .all .page{
    height:6%;
    width:100%;
    text-align: center;
  }
  .all .page .sel{
    color:pink
  }
  .all .page ul{
    list-style: none;
    height:100%;
    display: inline-block;
  }
  .all .page ul li{
    display: inline-block;
    float: left;
    cursor:pointer;
    border:1px solid pink;
    box-sizing: border-box;
    text-align: center;
  }

  .detail{
    position: fixed;
    height:70vh;
    width:40vw;
    left:50%;
    margin-left:-20vw;
    top:50%;
    margin-top:-35vh;
    background-color: #fff;
    border-radius:8px;
    box-shadow: 2px 2px 5px 0 gray;
  }
  .detail:after{
    content:'';
    display: block;
    height: 0;
    clear:both;
    visibility: hidden;
  }
  .detail>span{
    width:100%;
    float: left;
    font-size:30px;
    font-weight:bold;
    text-align: right;
    box-sizing: border-box;
    padding-right:20px;
    cursor:pointer;
  }

  .detail>img{
    width:100%;
    height:60%;
  }
  .detail>p{
    margin-top:3vh;
    width:100%;
    height:20px;
    padding-left:30px;
    padding-right:30px;
    box-sizing: border-box;
  }
  .detail>p span:first-child{
    float: left;
  }
  .detail>p span:last-child{
    float: right;
  }
  .full{
    position: fixed;
    right:0;
    top:0
  }
  ::-webkit-scrollbar-track-piece {
    background-color: translate;
    border-radius: 0;

  }

  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }

  ::-webkit-scrollbar-thumb {
    height: 50px;
    background-color: #999;
    outline: 2px solid #fff;
    outline-offset: -2px;
    border: 2px solid #fff;
    border-radius: 4px;
  }

  ::-webkit-scrollbar-thumb:hover {
    height: 50px;
    background-color: #9f9f9f;
    border-radius: 4px;

  }
</style>



