<template>
  <div class="hello">
    <div class="counter">
      <p>货柜</p>
      <div class="wrap" id="wrap"  @drop="drag($event)" @dragover="over($event)">
        <ul class="selGoods">
          <li v-show='selGoods.length > 0'>
            <div v-for='(ele,index) in selGoods' @drop="dragInLi($event,index)" @dragover="goodsOver($event,index)">
              <img v-if="ele.length > 0" v-for="(e,i) in ele"
                   @click="showInfor(index,i)"
                   @contextmenu="del($event,index,i)"
                   :width='ele.length === 1?"100%":(e.max === 4?"48%":"31%")'
                   :src="e.img" alt="" draggable="false">
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="all">
      <p>物品栏</p>
      <div class="search">搜索：<input type="text" placeholder="输入搜索内容..."></div>
      <ul class="allGoods">
        <li v-for="(e,i) in goodOnShow" @dragstart="startDrag($event,i)" draggable="true">
          <img :style="{width:e.width===100?'65%':e.width===50?'55%':'45%',
          height:e.width===100?'85%':e.width===50?'75%':'65%',
          marginTop:e.width===100?'3%':e.width===50?'5%':'8%'}"
               :src="e.img" alt="" draggable="true">
        </li>
      </ul>
      <div class="page">
        <ul>
          <li :class="page==i?'sel':''" v-for="i in pages" @click="changePage(i)">
            {{i}}
          </li>
        </ul>
      </div>
    </div>
    <input class="full" value="全屏" type='button' @click='fullScreen();' />
    <div class="detail" v-if="showDetail">
      <span @click = 'closeInfor'>×</span>
      <img :src="goodDetail.img" alt="">
      <p><span>商品名字 ：{{goodDetail.name}}</span><span></span></p>
      <p><span>编号 ：{{goodDetail.number}}</span><span>价格 ：{{goodDetail.price}}</span></p>
    </div>
  </div>
</template>

<script>
  import {goods} from './goods'
  export default {
    name: 'index',
    data () {
      return {
        allGoods:goods,
        goodOnShow:[],
        selGoods:[],

        goodDetail:{},
        showDetail:false,

        page:1,
        pages:0,

        add:false,
        index:null,
        num:false
      }
    },
    mounted(){
      this.pages = this.allGoods.length%24>0?
        parseInt(this.allGoods.length/24) +1 :
        this.allGoods.length/24
      this.changePage(1)
    },
    methods:{
      changePage(i){
        this.page = i;
        this.goodOnShow = this.allGoods.slice(24*(i-1),24*i)
      },
      startDrag(e,i){
        e.dataTransfer.setData("index" , i);
      },
      goodsOver(e,i){
        e.preventDefault();
        e.stopPropagation();
        this.resetAllStyle();
        if(!this.add){
          if(e.offsetX < 15){
            setTimeout(()=>{
              this.num = true
            },1000)
            if(this.num){
              this.add = true
              this.selGoods.splice(i,0,[])
              this.index = i
            }
          }else {
            this.num = false
            document.querySelectorAll('.selGoods li div')[i].style.border = '2px solid green';
          }
        }
      },
      drag(ev){

        let index = ev.dataTransfer.getData("index");
        this.selGoods.push([this.goodOnShow[index]]);

        setTimeout(()=>{
          let wrap = document.querySelector('#wrap').clientHeight;
          let selGoods = document.querySelector('#wrap .selGoods').clientHeight;
          if(selGoods>wrap){
            alert("商品槽位已满");
            this.selGoods.pop();
          }
        },0)

        this.resetAllStyle()
      },
      del(e,index,i){
        e.preventDefault();

        this.selGoods[index].splice(i,1);
        if(this.selGoods[index].length===0)
          this.selGoods.splice(index,1);
      },
      dragInLi(e,i){
        e.stopPropagation()
        let index = e.dataTransfer.getData("index");
        if(this.selGoods[i].length < 1){
          this.selGoods.splice(this.index,1,[this.goodOnShow[index]])
          this.add = false
          this.num = false
        }else {
          if(this.selGoods[i].length !== this.goodOnShow[index] && this.selGoods[i].length < this.selGoods[i][0].max){
            if(this.selGoods[i][0].max === this.goodOnShow[index].max){
              this.selGoods[i].push(this.goodOnShow[index]);
            }else {
              alert('当前商品尺寸与拖入商品尺寸不匹配')
            }
          }else {
            alert('已达数量上限')
          }
        }
        this.resetAllStyle()
      },
      resetAllStyle(){
        document.querySelectorAll('.selGoods li div').forEach((e)=>{
          e.style.border = '0';
        })
      },
      over(e){
        e.preventDefault();
      },
      fullScreen(){
        let element = document.documentElement;
        if(element.requestFullscreen) {
          element.requestFullscreen();
        } else if(element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if(element.webkitRequestFullscreen) {
          element.webkitRequestFullscreen();
        } else if(element.msRequestFullscreen) {
          element.msRequestFullscreen();
        }
      },
      showInfor(index,i){
        this.showDetail = true;
        this.goodDetail = this.selGoods[index][i]
      },
      closeInfor(){
        this.showDetail = false;
      },
    }
  }
</script>

<style scoped lang="scss">
  *{
    padding: 0;
    margin: 0;
    user-select: none;
  }
  .hello{
    width:86vw;
    margin:0 auto;
    padding-top:7.5vh;
  }
  .counter>p,.all>p{
    box-sizing: border-box;
    width:100%;
    line-height: 2.5;
    background-color: pink;
    text-align: center;
    font-size:25px;
    font-weight:bolder;
  }
  .all{
    margin-left:3vw;
    width:30vw;
    height: 85vh;
    float: left;

  }

  .counter{
    width:52vw;
    height: 85vh;
    float: left;
    .wrap {
      height: 90%;
      width: 100%;
      border: 1px solid pink;
      box-sizing: border-box;

      ul {
        width: 100%;
        list-style: none;
        background: url(../assets/bg.jpg) no-repeat;
        background-position: 0 118%;
        background-size: 100% 15.5vh;

        &:after {
          content: '';
          display: block;
          height: 0;
          visibility: hidden;
          clear: both;
        }
        li {
          padding: 1% 0;
          display: flex;
          flex-wrap: wrap;
          div{
            width: 20%;
            max-height: 140px;
            min-height: 120px;
            box-sizing: border-box;
            text-align: center;
            overflow-y: auto;
            padding-left:1%;
            padding-right:1%;
            display: flex;
            align-items: flex-start;
            flex-flow:column wrap;
            justify-content: flex-end;
            img {
              -webkit-box-flex:1;
              margin-bottom: 5%;
              box-shadow: 2px 2px 5px 0 gray;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
  .all .search{
    height:4%;
    border-left:1px solid pink;
    border-right:1px solid pink;
  }
  .all .search input{
    height:70%;
    width:50%;

  }
  .all .allGoods{
    height:80%;
    width:100%;
    list-style: none;
    border:1px solid pink;
    box-sizing: border-box;
  }
  .all .allGoods li{
    height:12.5%;
    width:33.3%;
    float: left;
    border:1px solid pink;
    box-sizing: border-box;
    text-align: center;
    line-height:12.5%

  }
  .all .allGoods li img{
    height:80%;
  }
  .all .page{
    height:6%;
    width:100%;
    text-align: center;
  }
  .all .page .sel{
    color:pink
  }
  .all .page ul{
    list-style: none;
    height:100%;
    display: inline-block;
  }
  .all .page ul li{
    display: inline-block;
    float: left;
    cursor:pointer;
    border:1px solid pink;
    box-sizing: border-box;
    text-align: center;
  }

  .detail{
    position: fixed;
    height:70vh;
    width:40vw;
    left:50%;
    margin-left:-20vw;
    top:50%;
    margin-top:-35vh;
    background-color: #fff;
    border-radius:8px;
    box-shadow: 2px 2px 5px 0 gray;
  }
  .detail:after{
    content:'';
    display: block;
    height: 0;
    clear:both;
    visibility: hidden;
  }
  .detail>span{
    width:100%;
    float: left;
    font-size:30px;
    font-weight:bold;
    text-align: right;
    box-sizing: border-box;
    padding-right:20px;
    cursor:pointer;
  }

  .detail>img{
    width:100%;
    height:60%;
  }
  .detail>p{
    margin-top:3vh;
    width:100%;
    height:20px;
    padding-left:30px;
    padding-right:30px;
    box-sizing: border-box;
  }
  .detail>p span:first-child{
    float: left;
  }
  .detail>p span:last-child{
    float: right;
  }
  .full{
    position: fixed;
    right:0;
    top:0
  }
  ::-webkit-scrollbar-track-piece {
    background-color: translate;
    border-radius: 0;

  }

  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }

  ::-webkit-scrollbar-thumb {
    height: 50px;
    background-color: #999;
    outline: 2px solid #fff;
    outline-offset: -2px;
    border: 2px solid #fff;
    border-radius: 4px;
  }

  ::-webkit-scrollbar-thumb:hover {
    height: 50px;
    background-color: #9f9f9f;
    border-radius: 4px;

  }
</style>



