let goods = []

 for(let i = 0;i<100;i++){
  let img,max
  if(i%3===0){
    img = 'http://img.juimg.com/tuku/yulantu/121020/240425-12102020030650.jpg';
    max = 4
  }else if(i%2===0){
    img = 'http://img3.imgtn.bdimg.com/it/u=2225473985,698261038&fm=15&gp=0.jpg';
    max = 9
  }else{
    img = 'http://mpic.tiankong.com/1a7/f3d/1a7f3d7a52c8698706041ae5ef43a0e4/640.jpg';
    max = 1
  }
  goods.push(
    { img ,name:'水果'+i,price:i+1,number:i,max:max},
  )
 }

export {goods}



